:root {
  // base
  --font-family: "Poppins", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
}
html,body{
  height: 100%;
}
.wrapper{
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main{
  flex-grow: 1;
}
section{
  margin-top: 100px;
}
.section__title {
  font-weight: 600;
  font-size: 40px;
  line-height: 36px;
  text-align: center;
  color: #363434;
}
